*{
    box-sizing: border-box;
    margin:0
}
.game-wrapper{
    display: flex;
    width: 100%;
    justify-content: center;
}
.game{

    position: relative;
    width: 800px;
    height:800px;
    background-color: aquamarine;
    padding: 0;
    margin:0 auto;
    line-height: 0;
}

.empty{

    display: inline-block;
    border: 1px solid rgb(255, 255, 255);
    background-color: rgb(148, 148, 148);
    align-items: center;
    justify-content: center;

}
.snake{
    display: inline-block;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(255, 255, 255);
    background-color: red;
}

.food{
    display: inline-block;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(255, 255, 255);
    background-color: yellow;
    margin: 0;
}
.tail{
    display: inline-block;
    align-items: center;
    justify-content: center;
    border: 1px solid rgb(255, 255, 255);
    background-color: rgb(255, 11, 133);
    margin: 0;
}

.button{
 width: 100%;
 height: 100%;
 font-size: 200px;
 border: none;
 outline: none;
 cursor: pointer;
}

.button--gameOver{
    position: absolute;
    left: 0;
    top: 0;
    z-index: 5;
}
.heading{
    text-align: center;
}